.new-products {
  padding: 7.113vw 0vw;
  &__tabs {
    margin-bottom: 1.604vw;
  }

  &__btn {
    text-align: center;
  }

  &__link {
    border: 0.052vw solid var(--main-black);
    color: var(--main-black);
    background-color: var(--light);
    &:hover {
      background-color: var(--main-black);
      color: var(--light);
    }
  }

  //radio-buttons
  .form {
    display: flex;
    flex-wrap: wrap;
  }

  .form__radio {
    &:not(:last-child) {
      margin-right: 0.26vw;
    }
  }

  .form__input[type="radio"] {
    display: none;
  }

  .form__label {
    display: inline-block;
    position: relative;
    padding-left: 1.302vw;
    margin-right: 0;
    line-height: 0.938vw;
    user-select: none;
    border-radius: 50%;
  }

  .form__radio-first:before,
  .form__radio-second:before,
  .form__radio-third:before {
    content: "";
    width: 0.781vw;
    height: 0.781vw;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 50%;
  }

  .form__radio-first:before {
    background-color: #bca685;
  }

  .form__radio-second:before {
    background-color: var(--main-black);
  }

  .form__radio-third:before {
    background-color: var(--gray);
  }

  /* Hover */
  .form__label:hover:before {
    filter: brightness(120%);
  }

  //tabs
  .tabs {
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 2.604vw;
    }

    &__nav {
      margin-bottom: 0.26vw;
    }

    &__nav-item {
      &:not(:last-child) {
        margin-right: 1.063vw;
      }
    }

    &__nav-btn {
      padding: 0.521vw 1.683vw;
      color: var(--main-black);
      font-weight: 400;
      font-size: 1.042vw;
      line-height: 1.1;
      border: 0.052vw solid transparent;
      span {
        font-weight: 400;
        font-size: 0.833vw;
        line-height: 1.1em;
        color: rgb(176, 171, 171);
        margin-left: 0.26vw;
      }
    }

    &__nav-btn--doors {
      background-color: var(--main-green-dark);
      color: var(--light);
    }

    &__nav-btn--parquet {
      background-color: var(--light);
      color: var(--main-black);
      border: 0.052vw solid var(--main-black);
    }

    &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 1.242vw;
      grid-row-gap: 1.242vw;
    }
  }
}

//product
.product {
  display: flex;
  flex-direction: column;
  padding: 1.798vw 1.563vw;
  background-color: var(--light);
  border-radius: 1vw;
  color: var(--main-black);
  transition: all 0.3s ease-in-out;
  &:hover {
    background: rgba(129, 107, 107, 0.3);
  }
  &__term {
    font-size: 0.938vw;
    color: var(--gray);
    line-height: 1.2;
    margin-bottom: 1.302vw;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 0 auto 0.598vw;
    img {
      height: 18.021vw;
      width: 9.292vw;
    }
  }

  &__title {
    margin-bottom: 0.885vw;
    font-family: var(--font-family-Helvetica);
    font-weight: 400;
    color: var(--main-color);
    font-size: 1.026vw;
    line-height: 1.2;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__color {
    span {
      display: inline-block;
      font-size: 0.938vw;
      line-height: 1.3;
      margin-bottom: 0.26vw;
    }
  }
}

//price
.price {
  &__old {
    font-size: 0.938vw;
    line-height: 1.3;
    color: var(--gray);
    text-decoration: line-through;
    text-align: right;
  }

  &__new {
    font-weight: 800;
    font-size: 1.25vw;
    line-height: 1.1;
  }
}
